import { Injection, Node } from '@ali/4ever-cangjie';
import { isLinkCardNode } from './isLinkCard';
import { ILinkCardInfo } from '../types';

export default function getCardInfo(node: Node, injections?: Injection[]): ILinkCardInfo | null | undefined {
  if (isLinkCardNode(node)) {
    const { data } = node;
    const { cardInfo } = data;
    if (cardInfo) {
      return cardInfo;
    }
  }

  if (injections && injections.length > 0) {
    const injection = injections.find((i) => i.key === node.key);
    if (injection) {
      return injection.data.cardInfo;
    }
  }

  return null;
}
